'use client';

import { useEffect } from 'react';
import GLightbox from 'glightbox';
import Choices from 'choices.js';
import Dropzone from 'dropzone';

export default function BootstrapClient() {
  useEffect(() => {
    // Inicializar Bootstrap
    typeof window !== 'undefined' && require('bootstrap/dist/js/bootstrap.bundle.min');

    // Inicializar GLightbox
    GLightbox({
      selector: '[data-gallery="image-popup"]',
      touchNavigation: true,
      loop: true,
    });

    // Inicializar Choices para los selects
    document.querySelectorAll('.js-choice').forEach((select) => {
      new Choices(select as HTMLSelectElement, {
        removeItemButton: true,
        shouldSort: false,
      });
    });

    // Inicializar Dropzone con tipado seguro
    Dropzone.autoDiscover = false;
    document.querySelectorAll('.dropzone').forEach((dropzone) => {
      const maxFiles = (dropzone as HTMLElement).dataset.dropzone;
      new Dropzone(dropzone as HTMLElement, {
        url: '/upload',
        maxFiles: maxFiles ? parseInt(maxFiles) : 1,
        acceptedFiles: 'image/*',
      });
    });
  }, []);

  return null;
}